<template>
  <div class="layout">
    <v-layout class="position-relative">
      <cartDrawer :windowWidth="windowWidth" />
      <menuDrawer :windowWidth="windowWidth" />
      <v-main
        :style="`padding-top:${
          $route.name == 'check_out'
            ? '0px'
            : windowWidth <= 990
            ? '60px'
            : '150px'
        } `"
      >
        <slot></slot>
      </v-main>
      <AppNav
        v-show="$route.name != 'check_out' && !showFixed && windowWidth > 990"
      />
      <respnsiveNav v-show="windowWidth <= 990 && $route.name != 'check_out'" />
      <fixedNav
        v-show="$route.name != 'check_out' && !showFixed && windowWidth > 990"
      />
      <AppFooter v-show="$route.name != 'check_out'" />
    </v-layout>
  </div>
</template>
<script>
import AppNav from "./AppNav.vue";
import AppFooter from "./AppFooter.vue";
import cartDrawer from "./cartDrawer.vue";
import fixedNav from "./fixedNav";
import respnsiveNav from "./responsiveNav.vue";
import menuDrawer from "./menuDrawer.vue";
export default {
  data: () => ({
    drawer: true,
    showFixed: false,
    windowWidth: 0,
  }),
  components: {
    AppNav,
    AppFooter,
    cartDrawer,
    fixedNav,
    respnsiveNav,
    menuDrawer,
  },
  mounted() {
    this.windowWidth = screen.width;
    window.onresize = () => {
      this.windowWidth = window.innerWidth;
    };
    window.onscroll = () => {
      if (window.scrollY >= 205) {
        this.showFixed = true;
      } else {
        this.showFixed = false;
      }
    };
  },
};
</script>
