.products-swiper {
  .swiper-button-next,
  .swiper-button-prev {
    width: 35px;
    height: 35px;
    border: 2px solid rgb(53, 53, 53);
    background-color: white;
    border-radius: 50%;
    top: 43%;
    &::after {
      font-size: 13px;
      display: flex;
      padding: 0;
      justify-content: center;
      align-items: center;
      color: rgb(53, 53, 53);
    }
  }
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    margin-top: 20px;
  }
}
.img-parent:hover {
  .quick-view-btn {
    opacity: 1 !important;
  }
}

@media (max-width: 580px) {
  .products-swiper {
    .img-parent {
      height: 300px !important;
    }

    .swiper-button-next,
    .swiper-button-prev {
      top: 56%;
    }
  }
}
</style>
