import { defineStore } from "pinia";
import axios from "axios";
export const productsMoudel = defineStore("productsMoudel", {
  state: () => ({
    flashDeals: [],
    newProducts: [],
    mobile: [],
    laptops: [],
    smartphones: [],
    singleProduct: "",
    categoryProducts: [],
    categories: [
      {
        title: "beauty",
        route: "beauty",
      },
      {
        title: "fragrances",
        route: "fragrances",
      },
      {
        title: "furniture",
        route: "furniture",
      },
      {
        title: "groceries",
        route: "groceries",
      },
      {
        title: "laptops",
        route: "laptops",
      },
      {
        title: "smartphones",
        route: "smartphones",
      },
      {
        title: "men's watches",
        route: "mens-watches",
      },
    ],
  }),
  actions: {
    async getProducts() {
      await axios
        .get("https://dummyjson.com/products?limit=190")
        .then((res) => {
          this.newProducts = res.data.products
            .filter((el) => el.category === "furniture")
            .slice(0, 8);
          this.flashDeals = res.data.products.slice(0, 8);
          this.mobile = res.data.products
            .filter((el) => el.category === "groceries")
            .slice(0, 8);
          this.laptops = res.data.products
            .filter((el) => el.category === "laptops")
            .slice(0, 8);
          this.smartphones = res.data.products
            .filter((el) => el.category === "smartphones")
            .slice(0, 8);
        })
        .catch((err) => console.log(err));
    },
    async getProductsByCategory(category) {
      await axios
        .get(`https://dummyjson.com/products/category/${category}`)
        .then((res) => (this.categoryProducts = res.data));
    },
    async getSingleProduct(productId) {
      await axios
        .get(`https://dummyjson.com/products/${productId}`)
        .then((res) => (this.singleProduct = res.data));
    },
  },
});
