<template>
  <div class="new-products pt-12">
    <div class="title mb-10 mx-5 d-flex align-center justify-space-between">
      <h3 style="font-weight: 900; font-size: 30px">New Products</h3>

      <router-link
        class="text-black"
        style="font-size: 14px"
        :to="{
          name: 'products_category',
          query: {
            title: categories[index].title,
            category: categories[index].route,
          },
        }"
        >Shop All</router-link
      >
    </div>
    <v-container fluid>
      <v-row>
        <v-col cols="7" class="pt-14" v-if="!products.length">
          <v-row>
            <v-col cols="12" sm="6" lg="4" v-for="num in 3" :key="num">
              <v-skeleton-loader
                type="image , article , button"
              ></v-skeleton-loader>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" md="7" class="pt-14 order-1 order-md-0" v-else>
          <Swiper
            :pagination="{ el: ' .swiper-pagination', clickable: true }"
            :modules="modules"
            :slides-per-view="3"
            :space-between="20"
            :breakpoints="breakpoints"
            class="pb-9 px-5"
          >
            <SwiperSlide
              v-for="item in products"
              :key="item.id"
              class="h-100"
              style="height: 500px"
            >
              <v-card elevation="0" class="pb-5 position-relative">
                <v-hover v-slot="{ isHovering, props }">
                  <div
                    class="img-parent"
                    style="height: 160px; overflow: hidden; position: relative"
                  >
                    <img
                      :src="
                        showenImages[item.title]
                          ? showenImages[item.title]
                          : item.thumbnail
                      "
                      alt=""
                      class="w-100"
                      :style="`height:100%;
            transition:0.5s all ease-in-out; scale:${
              isHovering ? 1.05 : 1
            }; cursor:pointer`"
                      v-bind="props"
                    />
                    <v-btn
                      density="compact"
                      width="60"
                      height="30"
                      class="bg-white quick-view-btn"
                      style="
                        text-transform: none;
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        border-radius: 30px;
                        font-size: 12px;
                        transition: 0.2s all ease-in-out;
                        opacity: 0;
                      "
                      variant="outlined"
                      @click="openQuickView(item)"
                      >Quick View</v-btn
                    >
                  </div>
                </v-hover>
                <v-card-text class="pl-0 pb-1">
                  {{
                    `(${item.title}) ${item.description}`.length <= 50
                      ? `(${item.title}) ${item.description}`
                      : `(${item.title}) ${item.description}`.substring(0, 50) +
                        "..."
                  }}
                </v-card-text>
                <v-rating
                  v-model="item.rating"
                  half-increments
                  readonly
                  color="yellow-darken-2"
                  size="x-small"
                  density="cobact"
                ></v-rating>
                <v-card-text class="pl-0 pt-0">
                  <del>${{ item.price }}</del> From
                  <span
                    class="text-red"
                    style="font-weight: 900; font-size: 16px"
                  >
                    ${{
                      Math.ceil(
                        item.price -
                          item.price * (item.discountPercentage / 100)
                      )
                    }}
                  </span>
                </v-card-text>
                <v-btn-toggle v-model="showenImages[item.title]" mandatory>
                  <v-btn
                    v-for="(pic, i) in item.images"
                    :value="pic"
                    :key="i"
                    size="x-small"
                    rounded="xl"
                    :ripple="flase"
                    ><img
                      :src="pic"
                      alt=""
                      width="30"
                      height="30"
                      style="
                        border-radius: 50%;
                        border: 1px solid rgb(135, 135, 135);
                      "
                  /></v-btn>
                </v-btn-toggle>
                <div class="mt-5">
                  <v-btn
                    density="combact"
                    class="py-2 px-12"
                    style="
                      text-transform: none;
                      border-radius: 30px;
                      position: absolute;
                      bottom: 0;
                    "
                    variant="outlined"
                    @click="
                      $router.push({
                        name: 'Product_Details',
                        params: { productId: item.id },
                      })
                    "
                    >Choose Options</v-btn
                  >
                </div>
              </v-card>
            </SwiperSlide>
            <div class="swiper-prev"></div>
            <div class="swiper-next"></div>
            <div class="swiper-pagination"></div>
          </Swiper>
        </v-col>
        <v-col cols="12" md="5">
          <img src="../../assets/images/vr-banner.webp" class="w-100" alt="" />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import { Pagination } from "swiper";
import { productsMoudel } from "@/stores/products";
import { mapState } from "pinia";
export default {
  inject: ["Emitter"],
  methods: {
    openQuickView(product) {
      this.Emitter.emit("openQuickView", product);
    },
  },
  computed: {
    ...mapState(productsMoudel, ["categories"]),
  },
  props: {
    products: {
      type: Array,
    },
    index: {
      type: Number,
    },
  },
  setup() {
    return {
      modules: [Pagination],
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  data: () => ({
    showenImages: {},
    breakpoints: {
      0: {
        slidesPerView: 1,
      },

      580: {
        slidesPerView: 2,
      },
      1316: {
        slidesPerView: 3,
      },
    },
  }),
};
</script>
<style lang="scss">
.products-swiper {
  .swiper-button-next,
  .swiper-button-prev {
    width: 35px;
    height: 35px;
    border: 2px solid rgb(53, 53, 53);
    background-color: white;
    border-radius: 50%;
    top: 43%;
    &::after {
      font-size: 13px;
      display: flex;
      padding: 0;
      justify-content: center;
      align-items: center;
      color: rgb(53, 53, 53);
    }
  }
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }
}
.img-parent:hover {
  .quick-view-btn {
    opacity: 1 !important;
  }
}

@media (max-width: 580px) {
  .products-swiper {
    .img-parent {
      height: 300px !important;
    }

    .swiper-button-next,
    .swiper-button-prev {
      top: 56%;
    }
  }
}
